<template>
    <v-container>
        <v-row>
            <v-col cols="6">
                <v-card height="600" color="#ab61c1">

                    <v-hover v-slot="{ hover }">
                        <v-btn color="#3d15b0" class="v-btn white--text" elevation="2" rounded
                        @click="navTo('addQuestion')"
                            :style="{ 'margin-left': '30%', 'margin-top': '30%', 'width': '40%', 'height': '40%', 'background-color': hover ? 'black' : '#BB86FC' }">
                            <h1>
                                اضافة سؤال
                            </h1>
                        </v-btn>
                    </v-hover>
                </v-card>
            </v-col>
            <v-col cols="6">
                <v-card height="600" color="#8e57b5">
                    <v-hover v-slot="{ hover }">
                        <v-btn color="#3d15b0" class="v-btn white--text" elevation="2" rounded
                        @click="navTo('explore')"
                            :style="{ 'margin-left': '30%', 'margin-top': '30%', 'width': '40%', 'height': '40%', 'background-color': hover ? 'black' : '#BB86FC' }">
                            <h1>
                                استعراض الاسئلة
                            </h1>
                        </v-btn>
                    </v-hover>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
import store from '@/store/store'
export default {
    computed: {
        ...mapGetters({
            getIsAdmin: 'getIsAdmin'
        })
    },
    methods: {
        navTo(s) {
            if (s == 'addQuestion') {
                this.$router.push('/new-question');
            } else {
                this.$router.push('/explore-questions');
            }
        }
    },
    beforeRouteEnter(to, from, next) {
        console.log(to, from)
    console.log('getIsAdmin', )
    if (store.state.isAdmin) {
     next() 
    } else {
        next('/')
    }
  },
}
</script>

